import React from 'react';
import { Icon, Tooltip } from 'antd';
import { withTranslation } from 'react-i18next';
import { inject, observer } from 'mobx-react';
import dayjs from 'dayjs';
import { get, observable } from "mobx"
import './style.scss';
import Loading from '../../common/Widget/Loading';
import Nodata from '../../common/Widget/Nodata';
import { getFile, storage } from '../../util/util';
import Checkbox from '../../common/Widget/Checkbox';
import DeleteModal from '../../common/Widget/DeleteModal';
import Createdbox from '../../common/Widget/Createdbox';
import LazyImage from '../../common/Lazy-image';
import imgload from '../../images/imgload.gif';
import imgload2 from '../../images/imgerror.png';
import event from '../../util/event';

//ls变量名称
const ALBUMSORT = 'AlbumSort';
const ALBUMSORT_1_ISDES = 'AlbumSort_isdes';

@withTranslation() @inject('photoalbum', 'login') @observer
class Photoalbum extends React.Component {
  constructor(props) {
    super(props);
    this.createdRef = React.createRef();
    this.contentRef = React.createRef();
    this.daughterRef = React.createRef();
  }
  @observable sortList = [
    { id: 1, text: 'updateSort' },
    { id: 2, text: 'creationSort' },
    { id: 3, text: 'photosSort' },
    { id: 4, text: 'nameSort' }
  ]
  @observable defaultSort = {}
  @observable createAlbum = false
  @observable renameModal = false;
  @observable ren_defaultValue = '';
  @observable renameAlbumId = null;
  @observable delete_visible = false;
  @observable a_deleteId = null;
  @observable sortindex = 0;
  @observable isdes = false;
  @observable sortDis = false;
  @observable menuDisplayId = null;

  componentDidMount() {
    let obj = {};
    const idx = storage.get(ALBUMSORT);
    const order = storage.get(ALBUMSORT_1_ISDES);
    if (!idx && idx !== 0) {
      storage.set(ALBUMSORT, 0)
      obj = this.sortList[0];
    } else {
      obj = this.sortList[idx];
      this.sortindex = idx;
    }
    this.defaultSort = obj;
    this.isdes = order || false;

    this.props.photoalbum.getPhotoalbum(storage.get(ALBUMSORT), this.isdes, this.contentRef.current.offsetWidth);

    document.addEventListener('click', this.morehide)
    document.addEventListener('click', this.sorthide)

    window.addEventListener('resize', this.getWH);

    this.wsinit = event.on('wsinit', (res) => {
      if (res === 1) {
        this.props.photoalbum.getPhotoalbum(storage.get(ALBUMSORT), this.isdes, this.contentRef.current.offsetWidth);
      }
    })
  }

  componentWillUnmount() {
    this.props.photoalbum.replacement();
    document.removeEventListener('click', this.morehide);
    document.removeEventListener('click', this.sorthide);
    window.removeEventListener('resize', this.getWH);
    event.rm(this.wsinit);
  }

  getWH = () => {
    this.props.photoalbum.resize(this.contentRef.current.offsetWidth, this.props.photoalbum.albumList)
  }

  //更多modal隐藏
  morehide = e => {
    if (e.target && e.target.nodeName === 'svg') {
      return;
    }
    this.menuDisplayId = null;
  }

  // 排序modal隐藏
  sorthide = e => {
    this.sortDis = false;
  }

  //更多modal显示
  more_show = (e, node) => {
    e.stopPropagation();
    if (this.menuDisplayId) {
      this.menuDisplayId = null;
    } else {
      this.menuDisplayId = node;
    }
  }

  //显示排序
  sortEldis(e) {
    e.nativeEvent.stopImmediatePropagation();
    this.menuDisplayId = null;
    this.sortDis = !this.sortDis;
  }
  //选择排序
  changeIndex(i) {
    storage.set(ALBUMSORT, i)
    if (this.sortindex === i) {
      this.isdes = !this.isdes;
      storage.set(ALBUMSORT_1_ISDES, this.isdes)
    } else {
      this.sortindex = i;
      storage.set(ALBUMSORT_1_ISDES, this.isdes);
    }
    this.defaultSort = this.sortList[i];
    this.props.photoalbum.album_sort(i, this.isdes);
  }
  //创建云相册确定
  cre_handleconfirm = (name) => {
    this.createAlbum = false;
    this.props.photoalbum.createAlbum(name);
  }
  //创建云相册取消
  cre_handlecancel = () => {
    this.createAlbum = false;
  }
  //重命名
  rename(e, name, id) {
    e.stopPropagation();
    this.renameModal = true;
    this.ren_defaultValue = name;
    this.renameAlbumId = id;
  }
  //重命名取消
  ren_handlecancel = () => {
    this.renameModal = false;
    this.ren_defaultValue = '';
  }
  //重命名确认
  ren_handleconfirm = (name) => {
    this.renameModal = false;
    this.props.photoalbum.renameAlbum(name, this.renameAlbumId);
  }
  //删除确认Modal取消
  del_handlecancel = () => {
    this.delete_visible = false;
    this.a_deleteId = null;
  }
  //删除确认Modal确认
  del_handleconfirm = () => {
    this.delete_visible = false;
    this.checkAll = false;
    this.props.photoalbum.album_delete(this.a_deleteId);
    this.a_deleteId = null;
  }
  //删除相册Modal打开
  delete(e, id) {
    e.stopPropagation();
    this.delete_visible = true;
    this.a_deleteId = [id];
  }

  //全选
  checkAllfn = () => {
    this.props.photoalbum.checkAllalbumlist()
  }

  //跳转相册详情
  goAlbumdetail = (e, id, name, created, updated) => {
    e.stopPropagation();
    this.props.photoalbum.goUrl({
      name, created, updated
    })
    this.props.history.push(`/album/albumdetails/${id}`)
  }


  render() {
    const { t } = this.props;
    const { OS } = this.props.login;
    const {
      sortList,
      defaultSort,
      createAlbum,
      renameModal,
      ren_defaultValue,
      delete_visible,
      isdes,
      sortDis,
      menuDisplayId,
      sortindex } = this;
    const { firstInit, albumList, pitchOn, removealbumList, invert } = this.props.photoalbum;
    const time = sortindex === 1 ? `${t('CreationTime')}：` : `${t('LastUpdate')}：`;
    let albumEl;
    if (firstInit) {
      if (albumList.length > 0) {
        albumEl = albumList.map((item) => {
          return (
            <div className='list' key={item.id} style={{ width: item.w, height: item.h }}>
              <div
                className={`album_list ${get(item, 'check') ? 'album_list-check' : null}`}
                onClick={(e) => { this.goAlbumdetail(e, item.id, item.name, item.created, item.updated) }}>
                <span
                  style={OS === 'tab' ? { visibility: 'inherit' } : {}}
                  className='icon-check-s treeNodeUnselectable'
                  onClick={(e) => { pitchOn(e, item.id, () => { this.checkAll = true }, () => { this.checkAll = false }) }} >
                  <i className='iconfont' style={get(item, 'check') ? {} : { visibility: "hidden" }}>&#xe62d;</i>
                </span>
                <div className='top-img' style={item.profile ? { backgroundImage: `url(${imgload})` } : { backgroundImage: `url(${imgload2})`, backgroundSize: '100% 100%' }}>
                  {item.profile && <LazyImage
                    src={getFile.file_s(item.profile.id)}
                    alt={item.profile.id}
                    w='226px' />}
                </div>
                <div className='bottom-information'>
                  <div className='name-time'>
                    <Tooltip placement="bottom" overlayClassName='tooltip' title={get(item, 'name')}>
                      <p className='album-name ellipsis'>{item.name}</p>
                    </Tooltip>
                    <p className='update-time'>
                      <span>{time}{sortindex === 1 ? dayjs(item.created).format(t('L')) : dayjs(item.updated ? item.updated : item.created).format(t('L'))}</span>
                    </p>
                  </div>
                  <div className='photoNum-more'>
                    <div className='photo-num'>
                      <p className='num1'>{item.filecount}</p>
                      <p className='text1'>{t('Photo')}</p>
                    </div>
                    <div className='more treeNodeUnselectable' onClick={(e) => { this.more_show(e, item.id) }}>
                      <span><Icon className='more-click' type="ellipsis" /></span>
                      <div className='rename' style={menuDisplayId === item.id ? { display: 'block' } : {}}>
                        <ul>
                          <li onClick={(e) => { this.rename(e, item.name, item.id) }}>
                            <span><Icon type="edit" theme="filled" />{t('rename')}</span>
                          </li>
                          <li onClick={(e) => { this.delete(e, item.id) }}>
                            <span><Icon type="delete" theme="filled" />{t('delete')}</span>
                          </li>
                        </ul>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          )
        })
      } else {
        albumEl = <Nodata />
      }
    } else {
      albumEl = <Loading />
    }
    let num = 0;
    removealbumList.forEach(item => {
      if (!item.true) {
        num += 1;
      }
    });

    return (
      <div className='photoalbum'>
        <div className='create-photoalbum'>
          <div className='create-photoalbum-btn'>
            <button onClick={() => { this.createAlbum = true }}>{t('NewAlbum')}</button>
          </div>
          <div className='photoalbum-num'>
            <div className='photoalbum-num-icon'><i className='iconfont'>&#xe934;</i></div>
            <div className='num-text'>
              <p className='text'>{t('cloudalbum')}</p>
              <p className='num'>{albumList.length}</p>
            </div>
          </div>
        </div>
        <div className='photoalbum-operation'>
          <div className='checkAll'>
            <i
              className={`iconfont check-photoalbum treeNodeUnselectable ${albumList.length === removealbumList.length && albumList.length > 0 ? 'icon-xuanzhong1' : 'icon-weixuanzhong'}`}
              onClick={this.checkAllfn}></i>
            <span className='checkAll-text'>{num > 0 ? `${t('Selected')} ${num} ${t('list')}` : t('Allelection')}</span>
            {removealbumList.length > 0 && <span onClick={invert} className='invert-Selection'>{t('Reverseselection')}</span>}
          </div>
          <div className='photoalbum-sort treeNodeUnselectable'>
            <span className='sort' onClick={(e) => { this.sortEldis(e) }}>{t(defaultSort.text)}{isdes ? <Icon type="arrow-up" /> : <Icon type="arrow-down" />}</span>
            <div className='sort-list' style={sortDis ? { display: 'block' } : { display: 'none' }}>
              <ul>
                {
                  sortList.map((item, index) => {
                    return (
                      <li key={item.id} onClick={() => { this.changeIndex(index) }}>
                        <span>
                          <Icon type="check" className={item.id === defaultSort.id ? 'check-icon' : null} />
                          {t(item.text)}</span>
                      </li>
                    )
                  })
                }
              </ul>
            </div>
          </div>
          <div
            className='photoalbum-delete'
            style={removealbumList.length > 0 ? null : { visibility: 'hidden' }}>
            <span onClick={() => { this.delete_visible = true; }}><Icon type="delete" />{t('delete')}</span>
          </div>
        </div>
        <div ref={this.contentRef} className='photoalbum-main'>
          {albumEl}
        </div>

        {
          createAlbum ? <Createdbox
            title={t('NewAlbum')}
            handlecancel={this.cre_handlecancel}
            handleconfirm={this.cre_handleconfirm} /> : null
        }

        {
          renameModal ? <Checkbox
            title={t('rename')}
            defaultValue={ren_defaultValue}
            handlecancel={this.ren_handlecancel}
            handleconfirm={this.ren_handleconfirm} /> : null
        }

        {
          delete_visible && <DeleteModal
            title={t('Deleteconfirm')}
            text={t('photoAlbum')}
            handlecancel={this.del_handlecancel}
            handleconfirm={this.del_handleconfirm} />
        }
      </div >
    );
  }
};

export default Photoalbum;
